import { Component, OnInit } from '@angular/core';
import { NavbarService } from '../../services/navbar.service';
import { OnDestroy } from '@angular/core/src/metadata/lifecycle_hooks';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss']
})
export class MainComponent implements OnInit, OnDestroy {

  public navbarIsOut: boolean = false;
  private navbarChangedSubscription: Subscription;

  constructor(private navbarService: NavbarService) { }

  ngOnInit() {
    this.navbarChangedSubscription = this.navbarService.navbarChanged.subscribe((next: boolean) => {
      this.navbarIsOut = next;
    })
  }

  ngOnDestroy() {
    this.navbarChangedSubscription.unsubscribe();
  }

}



