import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/Subject';

@Injectable()
export class NavbarService {

  private isNavBarOut: boolean;
  public navbarChanged = new Subject<boolean>();

  constructor() { }


  public navBarOut(): boolean {
    return this.isNavBarOut;
  }

  public toggleNavbar(): void {
    this.isNavBarOut = !this.isNavBarOut;
    this.navbarChanged.next(this.isNavBarOut);
  }
}



